main .main {
  border: 1px solid black;
  border-radius: 25px;
  width: 90%;
  position: relative;
  margin: 20px;
  background: white;
}
main .main__image img {
  object-fit: cover;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  width: 100%;
  height: auto;
}
main .main__heading {
  font-size: 12px;
  font-weight: 700;
  padding: 10px;
  color: var(--veryDarkGrayishBlue);
  text-align: left;
}
main .main__paragraph {
  font-size: 15px;
  font-weight: 400;
  padding: 10px;
  text-align: left;
  color: var(--DeasaturatedDarkBlue);
}
main .main__creator {
  padding: 10px;
  display: flex;
  justify-content: left;
  align-items: baseline;
}
main .main__creator--avatar img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  border-radius: 50%;
}
main .main__creator--name {
  text-align: left;
  font-weight: 300;
  color: var(--veryDarkGrayishBlue);
  font-size: 13px;
  padding: 10px;
  font-weight: 600;
  position: relative;
}
main .main__creator--name span {
  padding: 0;
  font-size: 12px;
  opacity: 0.8;
  font-weight: 400;
}
main .main__creator--share {
  position: absolute;
  bottom: 25px;
  right: 30px;
  background: var(--lightGrayishBlue);
  border-radius: 50%;
  width: 35px;
  height: 35px;
  text-align: center;
  padding: 3px;
  cursor: pointer;
}
main .main__creator--share img {
  vertical-align: middle;
  max-width: 100%;
  height: auto;
}
main .main__creator--share ul {
  background: var(--veryDarkGrayishBlue);
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}
main .main__creator--share ul li {
  list-style-type: none;
}
main .main__creator--share ul a {
  text-decoration: none;
}
main .main__creator--share ul a img {
  max-width: 100%;
  height: auto;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Manrope);
  background: var(--lightGrayishBlue);
}

:root {
  --veryDarkGrayishBlue: hsl(217, 19%, 35%);
  --DeasaturatedDarkBlue: hsl(214, 17%, 51%);
  --grayishBlue: hsl(212, 23%, 69%);
  --lightGrayishBlue: hsl(210, 46%, 95%);
}

:root {
  --font-Manrope: "Manrope", sans-serif;
}